<h1 id="less-命令" tabindex="-1"><a class="header-anchor" href="#less-命令" aria-hidden="true">#</a> less 命令</h1>
<h2 id="less-文件名" tabindex="-1"><a class="header-anchor" href="#less-文件名" aria-hidden="true">#</a> less 文件名</h2>
<p>less -N 文件名 显示文件行号</p>
<h2 id="常用指令" tabindex="-1"><a class="header-anchor" href="#常用指令" aria-hidden="true">#</a> 常用指令</h2>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code>j    下一行
k    上一行
f    向下滚动一屏幕
b    向上滚动一屏幕
g    定位到文档头部
G    定位到文档最尾部
q    退出less模式
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br></div></div><h2 id="查找内容" tabindex="-1"><a class="header-anchor" href="#查找内容" aria-hidden="true">#</a> 查找内容</h2>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code>/keyword　　向下查找
n    向下匹配下一处匹配文本
N    向上匹配下一处匹配文本

?keyword　　向上查找
n    向上匹配下一处匹配文本
N    向下匹配下一处匹配文本
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br></div></div><h2 id="实时查看文档变动" tabindex="-1"><a class="header-anchor" href="#实时查看文档变动" aria-hidden="true">#</a> 实时查看文档变动</h2>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code>F    　　　　实时滚动文档
Ctrl + c　　退出实时滚动模式

类似tail -f
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br></div></div>